%button {
  width: rem-calc(125);
  height: rem-calc(37.5);
  color: white;
  background: #ff227c;
  padding: 1rem;
  text-transform: uppercase;
  text-align: center;
  border-radius: 35px;
  border: 2px solid #e7e6e7;
  font-weight: bold;
  outline: none;
  font-size: 0.7rem;
  border: 0;
  cursor: pointer;

  &:hover {
    color: black;
  }
}

.button-primary {
  @extend %button;
  border: 0;
}

.button-secondary {
  @extend %button;
  width: 11%;
  margin: 0 auto;
  box-shadow: 0px 10px 25px rgba(234, 71, 138, 0.5);
}
